.CoinOnList {
  width: 100%;
  height: 100px;
  outline: 1px dashed #ccc;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.CoinOnListItem {
  width: 90%;
  height: 100px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.CoinOnList:nth-child(odd) {
  background: white;
}

@media (max-width: 800px) {
  .CoinOnList {
    width: 100%;
  }
}



